.input {
  border: none;
  margin: 0 0 25px;
  padding: 0;
  position: relative;
}

.input_is-auth {
  margin-bottom: 136px;
}

.input__text-field {
  width: 100%;
  color: #1e1e1e;
  background-color: #0000;
  border: none;
  border-bottom: 1px solid #2e69fd;
  outline: none;
  margin-bottom: 7px;
  padding-bottom: 7px;
  font-size: 15px;
  font-weight: 500;
  line-height: 15px;
}

.input__label {
  cursor: text;
}

.input__text {
  width: 100%;
  color: #999;
  font-size: 15px;
  font-weight: 500;
  line-height: 15px;
  position: absolute;
  top: 6px;
  left: 0;
}

.input__text_error {
  color: #fd0b0bf7;
}

.input__helper-text {
  min-height: 8px;
  color: #fd0b0bf7;
  opacity: 0;
  margin: 0;
  font-size: 8px;
  font-weight: 400;
  line-height: 8px;
}

.input__helper-text_show {
  opacity: 1;
}

.input__text_isValue {
  font-size: 9px;
  line-height: 9px;
  top: -10px !important;
}

